import { Pipe, PipeTransform } from '@angular/core';


import { Contact } from './models/contact';


@Pipe({
  name: 'fullName'
})
export class FullNamePipe implements PipeTransform {

  transform(c: Contact): string {
    return this.join(' ', c.title, c.givenname, c.maidenname, c.surname);
  }

  join(delimeter: string, ...args: string[]): string {
    let output = '';
    let first = true;
    for (let a of args) {
      if (!a || a === '') {
        continue;
      }
      output += (first ? '' : ' ') + a;
      first = false;
    }
    return output;
  }

}



