import { Component, Input, OnInit } from '@angular/core';

@Component({
  selector: 'v-hidden-text',
  template: `
  <abbr *ngIf='hideContent' [title]="title" (click)='show()'>{{placeholder}}</abbr>
  <ng-content *ngIf='!hideContent'></ng-content>
  `
})
export class HiddenTextDirective implements OnInit {

  @Input() title: string;
  @Input() placeholder: string;

  hideContent: boolean;

  constructor() { }

  ngOnInit() {
    this.hideContent = true;
  }

  show() {
    this.hideContent = false;
  }
}



