import { Component, OnInit, Input } from '@angular/core';

import { Contact } from '../models/contact';
import { Address } from '../models/address';
import { PaymentDetails } from '../models/paymentDetails';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'detail-view',
  templateUrl: './detail-view.component.html',
  styleUrls: ['./detail-view.component.scss']
})
export class DetailViewComponent implements OnInit {

  private _contact: Contact;
  get contact(): Contact { return this._contact; }
  @Input()
  set contact(value: Contact) {
    this._contact = value;
    this.address = {
      city: value.city,
      country: value.country,
      emailaddress: value.emailaddress,
      state: value.state,
      streetaddress: value.streetaddress,
      telephonecountrycode: value.telephonecountrycode,
      telephonenumber: value.telephonenumber,
      zipcode: value.zipcode
    };

    this.paymentDetails = {
      ccexpires: value.ccexpires,
      ccnumber: value.ccnumber,
      cctype: value.cctype,
      cvv2: value.cvv2,
      ccholder: value.givenname + ' ' + value.surname
    };

  }

  address: Address;
  paymentDetails: PaymentDetails;

  private _active: DetailPanels = DetailPanels.DetailInfo;


  get detailInfoActive(): boolean { return this._active === DetailPanels.DetailInfo; };
  get friendsActive(): boolean { return this._active === DetailPanels.Friends; };

  constructor() { }

  ngOnInit() {
  }

  showFriends() { this._active = DetailPanels.Friends; }
  showDetailInfo() { this._active = DetailPanels.DetailInfo; }

}

export enum DetailPanels {
  DetailInfo = 0,
  Friends = 1
}



