import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'ccardNumber'
})
export class CcardNumberPipe implements PipeTransform {

  transform(value: string, secret?: boolean): string {
    '5568 8714 0073 8693';
    if (!value) {
      return null;
    }

    let formattedValue = '';
    let needSpace = 0;
    let secretNums = secret ? 12 : 0;
    for (let char of value) {
      if (char < '0' || char > '9') {
        continue; // it's not a digit, skip
      }
      if (secretNums > 0) {
        formattedValue += '*';
        secretNums--;
      } else {
        formattedValue += char;
      }


      needSpace++;
      if (needSpace === 4) {
        formattedValue += ' ';
        needSpace = 0;
      }
    }
    return formattedValue;
  }

}



