import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import { Observable } from 'rxjs';
import 'rxjs';

import { Contact } from '../models/contact';


@Injectable()
export class ContactsService {

  constructor(private _http: Http) { }

  getContacts(top: number, skip: number, nameFilter: string): Observable<any> {
    return this._http.get(this.url(top, skip, nameFilter))
      .map((r: Response) => r.json())
      .catch((e, c) => {
        console.log('Error: ', e);
        return Observable.throw(e);
      });
  }

  private url(top: number, skip: number, nameFilter: string) {
    return `http://dataapi20161218050413.azurewebsites.net` +
      `/odata/Fakenames?$inlinecount=allpages&$top=${top}&$skip=${skip}` + 
      (nameFilter && nameFilter.length > 0 ? `&$filter=substringof(toupper('${nameFilter}'), toupper(givenname) )` : '');
  }

  getcontact() {
    return {
      middleinitial: "",
      statefull: "",
      zipcode: "",
      country: "",
      countryfull: "",
      emailaddress: "",
      username: "",
      password: "",
      browseruseragent: "",
      telephonenumber: "",
      telephonecountrycode: "",
      maidenname: "",
      birthday: "",
      age: "",
      tropicalzodiac: "",
      cctype: "",
      ccnumber: "",
      cvv2: "",
      ccexpires: "",
      nationalid: "",
      upstracking: "",
      westernunionmtcn: "",
      moneygrammtcn: "",
      color: "",
      occupation: "",
      company: "",
      vehicle: "",
      domain: "",
      bloodtype: "",
      pounds: "",
      kilograms: "",
      feetinches: "",
      centimeters: "",
      guid: "",
      latitude: "",
      longitude: "",
      gender: "",
      nameset: "",
      givenname: "",
      title: "",
      surname: "",
      streetaddress: "",
      city: "",
      state: "",
      rating: 5
    };
  }

}




