import {
  Directive,
  TemplateRef,
  ViewContainerRef,
  Input,
  OnInit,
  ChangeDetectorRef,
  DoCheck,
  OnChanges,
  SimpleChanges
} from '@angular/core';

@Directive({
  // tslint:disable-next-line:directive-selector
  selector: '[ngRange]',
  // tslint:disable-next-line:use-input-property-decorator
  // inputs: ['ngRangeOf', 'ngRangeFrom', 'ngRagneTo']
})
// tslint:disable-next-line:directive-class-suffix
export class NgRange implements OnInit, DoCheck, OnChanges {

  @Input() ngRangeOf: ((prev: any) => any) | string;
  @Input() ngRangeFrom: number;
  @Input() ngRangeTo: number;

  constructor(private _template: TemplateRef<NgRangeRow>,
    private _viewContainer: ViewContainerRef, private _cdr: ChangeDetectorRef) {

  }

  ngOnInit() {

    if (this.ngRangeFrom !== undefined && this.ngRangeTo !== undefined &&
      this.ngRangeOf !== undefined) {
      throw `You should use either ngRangeOf or ngRangeFrom/ngRangeTo, but not togather`;
    }

    if (this.ngRangeFrom !== undefined && this.ngRangeTo !== undefined) {
      this.trackingValuesGenerator();
    }
    if (typeof this.ngRangeOf === 'string') {
      this.useStringGenerator(this.ngRangeOf);
    } else if (typeof this.ngRangeOf === 'function') {
      this.useCustomGenerator(this.ngRangeOf);
    } else {
      throw `Illigal sequence: ${typeof this.ngRangeOf}`;
    }
  }
  ngOnChanges(changes: SimpleChanges): void {

  }

  ngDoCheck() {

  }
  trackingValuesGenerator() {

  }

  useStringGenerator(generator: string): void {
    let minMax = generator.split('..');
    let min = Number(minMax[0]);
    let max = Number(minMax[1]);

    if (min === undefined) {
      throw `Range "${generator}" should contain valid range start index`;
    }
    if (max === undefined) {
      throw `Range "${generator}" should contain valid range end index`;
    }

    const count = Math.abs(max - min);
    let i = min;
    let index = 0;
    while (i !== max) {
      const view = this._viewContainer.createEmbeddedView(this._template,
        new NgRangeRow(i, index, count), index);

      index++;
      if (max >= min) {
        i = i + 1;
      } else {
        i = i - 1;
      }
    }
  }

  useCustomGenerator(generator: (prev: any) => any): void {

    let value = generator(null);
    let i = 0;
    let count = null;
    while (value !== undefined) {
      let newValue = generator(value);

      if (newValue === undefined) {
        count = i;
      }
      const view = this._viewContainer.createEmbeddedView(this._template,
        new NgRangeRow(value, i, count));

      value = newValue;
      i++;
    }
  }
}

export class NgRangeRow {

  constructor(public $implicit: any, public index: number, public count: number) {
  }

  get first(): boolean { return this.index === 0; }

  get last(): boolean { return this.index === this.count - 1; }

  get even(): boolean { return this.index % 2 === 0; }

  get odd(): boolean { return !this.even; }
}



