import { Component, OnInit, Input } from '@angular/core';

import { Address } from '../models/address';
import { PaymentDetails } from '../models/paymentDetails';

@Component({
  selector: 'app-address',
  template: `
        <address>
          <strong>Billing address<i class='glyphicon glyphicon-pencil' (click)='editAddress()'></i></strong>
          <br> {{value.streetaddress}}
          <br> {{value.city}}, {{value.state}} {{value.zipcode}} {{value.country}}
          <br>
          <strong>Phone </strong>+{{value.telephonecountrycode}} {{value.telephonenumber}}
          <br>
          <strong>Email </strong><a href="mailto:#">{{value.emailaddress}}</a>
        </address>
  `,
})
export class AddressComponent {
  @Input() value: Address;

  editAddress() {

  }
}

@Component({
  selector: 'app-payment-details',
  template: `
        <strong>{{value.cctype}}</strong>
        <br>{{value.ccholder}}
        <br><v-hidden-text placeholder="{{value.ccnumber | ccardNumber:true}}" > {{value.ccnumber | ccardNumber}} </v-hidden-text>
        <br>Expires at {{value.ccexpires}}
        <br> <v-hidden-text title='Show cvv2'
                placeholder='Cvv2: ***'>Cvv2: {{value.cvv2}}</v-hidden-text>
  `,
})
export class PaymentDetailsComponent {
  @Input() value: PaymentDetails;
}



