import { Component, OnInit } from '@angular/core';

import { ContactsService } from '../data.service/contacts.service';
import { Contact } from '../models/contact';
import { PaginationSettings } from '../../app-common/pager/pagination-settings';


@Component({
  // tslint:disable-next-line:component-selector
  selector: 'list-view',
  templateUrl: './list-view.component.html',
  styleUrls: ['./list-view.component.scss']
})
export class ListViewComponent implements OnInit {

  rows: TableRow<Contact>[];

  selectedRows: Set<TableRow<Contact>>;
  selectMode: SelectMode = SelectMode.SingleRow;
  page: PaginationSettings = new PaginationSettings(1, 50, 1);

  hasdata = false;

  nameFilter: string = '';

  constructor(private _data: ContactsService) { }


  ngOnInit() {
    this.loaddata(this.page);

    this.selectedRows = new Set<TableRow<Contact>>();
  }

  gotoPage(page: number) {
    this.hasdata = false; // refresh pager because ngRange doesn't track changes yet
    this.loaddata(this.page);
  }
  toogleRow(row: TableRow<Contact>) {
    row.expanded = !row.expanded;
  }

  select(event: Event, row: TableRow<Contact>) {
    event.preventDefault();
    if (this.selectedRows.has(row)) {
      this.selectedRows.delete(row);
    } else {
      if (this.selectMode === SelectMode.SingleRow) {
        this.selectedRows.clear();
      }
      this.selectedRows.add(row);
    }
  }

  search() {
    this.loaddata(new PaginationSettings(1, 50, 1));
  }
  
  loaddata(paginationSettings: PaginationSettings) {
    this._data.getContacts(paginationSettings.pageSize, paginationSettings.pageSize * (paginationSettings.currentPage - 1), this.nameFilter)
      .subscribe(r => {
        let numberOfItems = r['odata.count'];
        this.page = new PaginationSettings(paginationSettings.currentPage, paginationSettings.pageSize, numberOfItems);
        this.rows = r.value.map((d) => new TableRow<Contact>(d));
        this.hasdata = true;
      });
  }
}

export class TableRow<T>{
  expanded: boolean = false;
  data: T;

  constructor(data: T) {
    this.data = data;
  }
}
export enum SelectMode {
  SingleRow = 0,
  Multiselect = 1
}



