import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { PaginationSettings } from './pagination-settings';


@Component({
  selector: 'app-pager',
  template: `
  <div class="btn-group" role="group">
  <button type="button" class="btn btn-default" (click)='goto(settings.firstPage)'>First</button>

  <a class="btn" *ngIf='leftBound > 1' (click)='goto(leftBound - 1)'> . . . </a>

  <button type="button" class="btn btn-default" *ngRange='let pageNum from leftBound to rightBound'
    [class.active]='pageNum===settings.currentPage' (click)='goto(pageNum)'>
        {{pageNum}} 
  </button>

  <a class="btn" *ngIf='rightBound < settings.numberOfPages' (click)='goto(rightBound)'> . . . </a>

  <button type="button" class="btn btn-default" (click)='goto(settings.lastPage)'>Last</button>
</div>
`
})
export class PagerComponent implements OnInit {

  @Input() settings: PaginationSettings;
  @Output() pageChanged: EventEmitter<number> = new EventEmitter<number>();

  leftBound: number = 0;
  rightBound: number = 0;


  constructor() { }

  ngOnInit() {
    this.recalculate();
  }

  recalculate() {
    if (this.settings.numberOfPages <= this.settings.maxPagesToShow) {
      this.leftBound = 1;
      this.rightBound = this.settings.numberOfPages;
    } else {

      let half = Math.floor(this.settings.maxPagesToShow / 2);
      let quarter = Math.floor(half / 2);

      // current page is in first frame
      if (this.settings.currentPage < this.settings.maxPagesToShow) {
        this.leftBound = 1;
        this.rightBound = this.settings.maxPagesToShow;

      } else
        // current page is in middle between first and last frames
        if (this.settings.currentPage >= this.settings.maxPagesToShow &&
          this.settings.currentPage < (this.settings.numberOfPages - this.settings.maxPagesToShow)) {

          // let framesCount =  Math.floor(this.settings.number / this.settings.maxPagesToShow);
          let currentFrame = Math.floor(this.settings.currentPage / this.settings.maxPagesToShow);
          this.leftBound = currentFrame * this.settings.maxPagesToShow;
          this.rightBound = this.leftBound + this.settings.maxPagesToShow;

        } else
          // current page is in last frame
          if (this.settings.currentPage > (this.settings.numberOfPages - this.settings.maxPagesToShow)) {
            this.leftBound = this.settings.numberOfPages - this.settings.maxPagesToShow;
            this.rightBound = this.settings.numberOfPages + 1; // ngRange doesn't include right bound
          }
    }
  }

  goto(pageNumber: number) {

    console.log('goto page: ', pageNumber);
    this.settings.currentPage = pageNumber;
    //  this.recalculate();
    this.pageChanged.emit(pageNumber);
  }

}





