export class PaginationSettings {

  maxPagesToShow: number = 10;
  numberOfPages: number;
  lastPage;
  firstPage;

  constructor(public currentPage: number,
    public pageSize: number,
    public numberOfItems: number) {

    let pagesWithTail = numberOfItems / pageSize;
    this.numberOfPages = Math.floor(pagesWithTail);
    this.firstPage = 1;
    if ((pagesWithTail - this.numberOfPages) === 0) {
      this.lastPage = this.numberOfPages;
    } else {
      this.lastPage = this.numberOfPages = this.numberOfPages + 1;
    }
  }
}



